*, *::before, *::after {
  box-sizing: border-box;
}
[class*="col-"] {
  width: 100%;
}
body {
  margin: 0px;
  font-family: sans-serif;
  background-color: #bd560d;
}
header {
  background-color: #f57114;
  text-align: center;
  padding: 50px;
}
.wit {
  color: white;
}
menu {
  list-style-type: none;
  margin: 0%;
  padding: 0%;
  background-color: #bd560d;
}
li a {
  display: block;
  color: white;
  text-align: center;
  padding: 15px;
  text-decoration: none;
}
hr {
  border: 2px solid yellow;
  margin: 0%;
}
.sub {
  font-size: smaller;
  font-weight: bold;
}
img {
  width: 100%;
}
#main hr {
  all: initial;
  display: block;
  margin-top: 0.5em;
  margin-bottom: 0.5em;
  margin-left: auto;
  margin-right: auto;
  border-style: inset;
  border-width: 1px;
}
figure {
  margin: 15px;
}
#links {
  background-color: #ffb785;
  padding: 15px;
}
address {
  font-style: normal;
}
.zwart {
  color: black;
}
iframe {
  width: 100%;
  height: 400px;
  border: 0;
}
#rechts {
  background-color: white;
  padding: 15px;
}
#rechts h2 {
  color: #bd560d;
}
#rechts img {
  width: 33%;
}
#f_links {
  float: left;
  width: 33%;
  margin: 15px;
  margin-top: 50px;
}
#f_rechts {
  float: right;
  width: 33%;
  margin: 15px;
}
article {
  font-size: small;
}
input, select, textarea {
  width: 100%;
  border-radius: 3px;
  margin-top: 5px;
  padding: 15px;
  border: 1px solid #cccccc;
}
input[type="submit"], button {
  width: initial;
  background-color: #d15f0f;
  color: white;
  border: 0;
  border-radius: 3px;
  padding: 15px;
}
footer {
  background-color: #d15f0f;
  height: 115px;
  text-align: center;
  padding: 45px;
  font-weight: bold;
  font-size: small;
}
#dialog {
  display: none;
}
@media only screen and (min-width: 768px) {
  [class*="col-"] {
      float: left;
  }
  .col-1 {width: 33.33%;}
  .col-2 {width: 66.66%;}
  .col-3 {width: 100%;}
  body {
      font-size: 1.3vw;
  }
  li {
      float: left;
  }
  .huidig {
      background-color: #f57114;
  }
  a:hover {
      background-color: #f57114;
  }
  #main {
      display: flex;
  }
  #rechts img {
      width: calc(50% - 29px);
  }
  article:hover {
      cursor: pointer;
  }
}